@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;600;800&display=swap);
body,
body * {
  margin: 0;
  padding: 0;
  vertical-align: top;
  box-sizing: border-box;
  font-family: 'Nunito Sans', sans-serif;
  word-break: break-all;
}

a {
  color: black;
  text-decoration: none;
}
input, button{
  outline: none;
  /* border: none */
}

.dark {
  background-color: hsl(209, 23%, 22%);
  color:hsl(0, 0%, 100%);
}
.dark-bg {
 background-color: hsl(207, 26%, 17%); 
}
body, .App {
  min-height: 100vh;
}
.header-hidden {
    display: none;
}
.light {
  color:  hsl(200, 15%, 8%);
  background-color: hsl(0, 0%, 100%);
}
.light-bg {
  background-color: hsl(0, 0%, 98%);
}

header {
    width: 100%;
    height: 10vh;
    margin-bottom: 30px;
    box-shadow: 0 4px 5px -5px rgb(77, 75, 75);
}
header .header-container {
    margin: 0 auto;
    width: 90%;
    height: 95%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 20px;
}
header .header-container h3 {
    font-size: 20px;
}
.mode {
    position: relative;
    cursor: pointer;
}
.sun {
    left: -50px;
    height: 30px !important;
    width: 30px !important;
}

@media (max-width:500px) {
    header .header-container h3  {
        font-size: 15px;
    }
    header .header-container span  {
        font-size: 15px;
}


}

.search-box {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom:30px;
    flex-direction: column;
}
.search-box > form {
    display: flex;
    flex-direction: column;
    width: 85%;
    max-width: 500px;
    height: 40px;
    align-items: center;
    justify-content: center;
    position: relative;
}
.search-box form input {
     width: 100%;
    height: 100%;
    border: none;
    padding: 10px;
    outline: none;
    position: relative;
    box-shadow: 0px 0px 5px 3px rgba(0,0,0,0.19);
    /* box-shadow: 0px 0px 5px 3px hsl(207, 26%, 17%);     */
}
.search-box form .custom-select {
    width: 30%;
    height: 100%;
    padding: 10px;
    border-radius: 0;
    outline: none;
    border:none;
    box-shadow: 0px 0px 5px 3px rgba(0,0,0,0.19);
}

.custom-option:hover {
    background-color: rgb(83, 74, 83);
}
.search-box form button {
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
}
.icon {
    position: absolute;
    top: 0;
    right: 10px;
    top: 50%;
    bottom: 50%;
    transform: translateY(-50%);
    height: 20px;
    width: 20px;
}

.filter-box {
    width: 100%;
    height: 40px;
    display: flex;
    margin: 20px 0;
    align-items: center;
    justify-content: center;

}
.filter-box > form {
    width: 85%;
    height: 100%;
    max-width: 500px;

}

.filter-box > form select {
height: 100%;
width: 70%;
border-radius: 5px;
} 

@media (min-width:800px) {
    .search-box {
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
    }
    .filter-box {
        width: 40%;
    }
    .search-box > form {
        margin-left: 50px;
        width: 60%;
    }
}

.cards-section {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
}

.cards-section .cards-container {
    width: 90%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: center;
}

@media(max-width:1000px) {
    .cards-section .cards-container {
        justify-content: center;
    }
}

.cards-section .cards-container .single-card {
    width: 350px;
    height: 350px;
    margin: 20px;
    border-radius: 20px;
    cursor: pointer;
    box-shadow:         1px 1px 4px 1px rgba(112, 111, 111, 0.5);
}

.cards-section .cards-container .single-card img {
    height: 50%;
    width: 100%;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
}

.cards-section .cards-container .single-card .card-text {
    width: 80%;
    height: 38%;
    text-align: left;
    margin: 0 auto;
    margin-top: 3%;
}

.cards-section .cards-container .single-card .card-text p {
    margin: 12px 0;
    font-weight: bold;
}

.cards-section .cards-container .single-card .card-text span {
    font-weight: 500;
}

.single-card-page {
    width: 90%;
    min-height: 80vh;
    margin: 10px auto;
    display: flex;
    flex-direction: row;
}

.single-card-page .left-container{
    width: 40%;
    margin-right: 5%;
    margin-top: 30px;
}

.single-card-page .left-container img{ 
width: 90%;
height: 400px;

}
.single-card-page .right-container {
    display: flex;
    width: 55%;
    margin-top: 120px;


}
.single-card-page p {
    font-weight: bold;
}
.single-card-page span {
    font-weight: lighter;
}
.single-card-page .right-container .info1 , .single-card-page .right-container .info2 {
    width: 50%;
}
.single-card-page .right-container .info2 {
    margin-top: 60px;
}
.single-card-page .right-container .info1 h1 {
    height: 60px;
}
.single-card-page .right-container p {
    margin: 30px 0;
}

.go-back {
    height: 60px;
    width: 150px;
    padding: 10px;
    margin-bottom: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid black;
    cursor: pointer;
    position: relative;
}
.arrow {
    width: 30px !important;
    height: 30px !important;
    left: 5px;
}
 @media(max-width: 1000px) {

    .single-card-page {
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    .single-card-page .left-container, .single-card-page  .right-container {
        width: 80%;
        margin-left: 0;
        margin-right: 0;
    }

    .single-card-page  .right-container {
        flex-direction: column;

    }
    .single-card-page .right-container .info2 {
        margin-top: 0;
    }
 }
